body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.reaction {
    display: flex;
    font-size: 19.2px;
}

.reaction__likes, .reaction__dislikes {
    display: flex;
    align-self: baseline;
    cursor: pointer;

    -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */ /* Konqueror HTML */
    -moz-user-select: none; /* Firefox */
    -ms-user-select: none; /* Internet Explorer/Edge */
    user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome and Opera */
}

.reaction__dislikes {
    margin-left: 10px;
}

.reaction__likes-icon, .reaction__dislikes-icon {
    align-self: center;
}

.reaction__likes-count, .reaction__dislikes-count {
    margin: 0 0 0 10px;
}

.reaction__likes-icon, .reaction__likes-count {
    color: #F20094;
}

.reaction__dislikes-icon, .reaction__dislikes-count {
    color: #222222;
}
